import { T_ClockDataOfTimeZone } from "shared/helpers/timeZone/types";
import { T_Nullable } from "shared/types/generics";

export enum E_Meridiem {
  AM = "AM",
  PM = "PM"
};

export type T_Degree = number;

export type T_HourOrMinute = number;

export type T_Time24HrsFormat = {
  hour: T_HourOrMinute;
  minute: T_HourOrMinute;
};

export type T_TimeForDisplay = {
  hour: string;
  minute: string;
  meridiem?: T_Nullable<E_Meridiem>;
};

export type T_TimeRangeDegreeValues = T_Degree[];

export type T_TimeRangeTimeValuesForDisplay = T_TimeForDisplay[];

export interface T_TimeRange {
  degreeValues: T_TimeRangeDegreeValues;
  timeValuesForDisplay: T_TimeRangeTimeValuesForDisplay;
}

export type T_ClockDataOfTimeZoneWithTimeRange = T_ClockDataOfTimeZone & {
  timeRange: T_TimeRange;
}

export type T_TimeRangeSelectorState = {
  timeZonesData: T_ClockDataOfTimeZoneWithTimeRange[];
  is24HourFormat: boolean;
};

export enum E_TimeRangeSelectorTimeZoneListItemActions {
  setAsMain = "SET AS MAIN",
  change = "CHANGE",
  remove = "REMOVE",
}

export enum E_TimeRangeSelectorActions {
  setTimeRangeHandle,
  toggle24HourFormat,
  setMainTimeZone,
  changeTimeZone,
  removeTimeZone
}

export type T_TimeRangeSelectorAction = [
  type: E_TimeRangeSelectorActions,
  payload?: any
];

export interface T_TimeRangeSelectorClockMarksMap {
  [x: T_Degree | string]: T_HourOrMinute;
};
