import { DateTime } from "luxon";
import { SliderProps } from "rc-slider";
import { MarkObj } from "rc-slider/lib/Marks";

import { toNumber } from "shared/helpers/number";
import { getClocksDataOfLocalAndOtherTimeZones, localTimeZoneName } from "shared/helpers/timeZone";
import { T_Nullable } from "shared/types/generics";
import { E_Breakpoint } from "shared/types/window";
import toolsTimeTimeZoneConstants from "./constants";
import {
  T_Degree,
  T_Time24HrsFormat,
  T_TimeForDisplay,
  E_Meridiem,
  T_HourOrMinute,
  T_TimeRangeSelectorClockMarksMap,
  T_TimeRangeSelectorState,
  T_TimeRangeSelectorAction,
  E_TimeRangeSelectorActions,
  E_TimeRangeSelectorTimeZoneListItemActions
} from "./types";

export function get12HourValue(value: T_HourOrMinute) {
  const twelveHourValue = value % 12;
  return twelveHourValue ? twelveHourValue : 12; // the hour '0' should be '12'
}

export function getMeridiemFrom24HourValue(value: number) {
  return value >= 12 ? E_Meridiem.PM : E_Meridiem.AM
}

export function get24HourValue(
  value: string,
  meridiem?: T_Nullable<E_Meridiem>
) {
  const numericValue = toNumber(value);

  if (meridiem) {
    if (meridiem === E_Meridiem.PM) {
      if (numericValue < 12) {
        return numericValue + 12;
      }
    } else if (numericValue === 12) {
      return 0;
    }
  }

  return numericValue ?? 0;
}

function getTimeForDisplay(
  time: T_Time24HrsFormat,
  isTarget24HourFormat: boolean
): T_TimeForDisplay {
  let { hour, minute } = time;
  let meridiem = null;

  if (!isTarget24HourFormat) {
    meridiem = (hour >= 12 && hour !== 24) ? E_Meridiem.PM : E_Meridiem.AM;
    hour = get12HourValue(hour);
  }

  return {
    hour: String(hour).padStart(2, "0"),
    minute: String(minute).padStart(2, "0"),
    meridiem
  };
}

export function getTimeFromDegreeForDisplay(
  degree: T_Degree,
  isTarget24HourFormat: boolean
): T_TimeForDisplay {
  let hour = 0;
  let minute = 0;

  if (degree !== 0) {
    minute = degree % toolsTimeTimeZoneConstants.degreePerClockMark;
    hour = (degree - minute) / toolsTimeTimeZoneConstants.degreePerClockMark;
  }

  return getTimeForDisplay({ hour, minute }, isTarget24HourFormat);
}

export function getDegreeFromTime(time: T_TimeForDisplay) {
  const hourIn24HourFormat = get24HourValue(time.hour, time.meridiem);

  return (
    hourIn24HourFormat * toolsTimeTimeZoneConstants.degreePerClockMark +
    toNumber(time.minute)
  );
}

export function getClockMarksDeviceStyleOptimized(
  isTarget24HourFormat: boolean,
  activeBreakpoint?: E_Breakpoint
) {
  const clockMarks: SliderProps["marks"] = {};

  for (
    let index = 0;
    index <= toolsTimeTimeZoneConstants.clockMarkCount;
    index++
  ) {
    const key = index * toolsTimeTimeZoneConstants.degreePerClockMark;
    const label = isTarget24HourFormat ? index : get12HourValue(index);
    let value: React.ReactNode | MarkObj;

    switch (activeBreakpoint) {
      case E_Breakpoint.none: {
        value = {
          style: {
            fontSize: 8,
            marginTop: -3
          },
          label
        };
        break; 
      }
      case E_Breakpoint.small:
      case E_Breakpoint.medium: {
        value = {
          style: {
            fontSize: 10,
            marginTop: -2
          },
          label
        };
        break; 
      }
      default: {
        value = label
      }
    }

    clockMarks[key] = value;
  }

  return clockMarks;
}

export function getSliderStylePropsDeviceStyleOptimized(sliderProps: SliderProps, activeBreakpoint?: E_Breakpoint) {
  let newSliderProps = { ...sliderProps } as SliderProps;
  let handleStyle;
  let dotStyle;
  let trackStyle;
  let railStyle;

  switch (activeBreakpoint) {
    case E_Breakpoint.none: {
      const dotTrackRailHeight = 20;
      handleStyle = {
        borderWidth: 2,
        width: 10
      };
      dotStyle = {
        height: dotTrackRailHeight,
        borderWidth: 1
      }
      trackStyle = {
        height: dotTrackRailHeight
      }
      railStyle = {
        height: dotTrackRailHeight
      }
      break;
    }
  }

  if (Array.isArray(sliderProps.handleStyle) && handleStyle) {
    newSliderProps.handleStyle = [
      { ...sliderProps.handleStyle[0], ...handleStyle },
      { ...sliderProps.handleStyle[1], ...handleStyle }
    ];
  }
  if (sliderProps.dotStyle && dotStyle) {
    newSliderProps.dotStyle = { ...sliderProps.dotStyle, ...dotStyle };
  }
  if (sliderProps.trackStyle && trackStyle) {
    newSliderProps.trackStyle = { ...sliderProps.trackStyle, ...trackStyle };
  }
  if (sliderProps.railStyle && railStyle) {
    newSliderProps.railStyle = { ...sliderProps.railStyle, ...railStyle };
  }

  return newSliderProps;
}

export function convertDegreeValuesToTimeValuesForDisplay(
  degreeValues: T_Degree[],
  isTarget24HourFormat: boolean
) {
  return degreeValues.map((value) =>
    getTimeFromDegreeForDisplay(value, isTarget24HourFormat)
  );
}

export function convertDateTimeValuesToDegreeValues(
  dateTimeValues: DateTime[]
) {
  return dateTimeValues.map((value) =>
    getDegreeFromTime({
      hour: value.hour.toString(),
      minute: value.minute.toString()
    })
  );
}

export function convertTimeValuesToDegreeValues(
  timeValues: T_TimeForDisplay[]
) {
  return timeValues.map((value) =>
    getDegreeFromTime({
      hour: value.hour,
      minute: value.minute,
      meridiem: value.meridiem as E_Meridiem
    })
  );
}

export function getDegreeValuesWithoutOverflow([from, to]: number[]) {
  let maxLimitedTo = to;

  if (from > to) {
    maxLimitedTo = toolsTimeTimeZoneConstants.clockMarkDegreeRange.max    
  }

  return [from, maxLimitedTo];
}

const clocksData = getClocksDataOfLocalAndOtherTimeZones([
  "Asia/Kolkata",
  "Pacific/Auckland",
  "Asia/Tokyo",
  "Europe/London",
  "Europe/Berlin",
].filter(tz => tz !== localTimeZoneName));
const is24HourFormat = false;
const [clocksDataOfMainTimeZone] = clocksData;
const timeZonesData = clocksData.map((clock, index) => {
  const nextHourValue = get24HourValue(
    clocksDataOfMainTimeZone.hour,
    clocksDataOfMainTimeZone.meridiem as E_Meridiem
  ) + 1;
  const nextNextHourValue = nextHourValue + 1;
  const degreeValues = convertTimeValuesToDegreeValues([
    {
      hour: nextHourValue.toString(),
      minute: "0",
      meridiem: getMeridiemFrom24HourValue(nextHourValue)
    },
    {
      hour: nextNextHourValue.toString(),
      minute: "0",
      meridiem: getMeridiemFrom24HourValue(nextNextHourValue)
    }
  ]);
  const degreeValuesWithoutOverflow = getDegreeValuesWithoutOverflow(degreeValues);
  const timeValuesForDisplay = convertDegreeValuesToTimeValuesForDisplay(
    degreeValuesWithoutOverflow,
    is24HourFormat
  );

  return {
    ...clock,
    timeRange: {
      degreeValues: degreeValuesWithoutOverflow,
      timeValuesForDisplay
    }
  };
});

export const initialState: T_TimeRangeSelectorState = {
  timeZonesData,
  is24HourFormat
};

export function getTimeRangeSelectorEventHandlers(dispatch: React.Dispatch<T_TimeRangeSelectorAction>
  ) {
  const handle24HourFormatToggle = () => {
    dispatch([E_TimeRangeSelectorActions.toggle24HourFormat]);
  };
  const handleTimeRangeChange = (value: T_Degree | T_Degree[]) => {
    dispatch([E_TimeRangeSelectorActions.setTimeRangeHandle, value]);
  };
  const handleTimeZoneListItemAction = (
    event: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    const { timeZoneName, actionName } = event.currentTarget.dataset;
    if (timeZoneName && actionName) {
      switch (actionName as unknown as E_TimeRangeSelectorTimeZoneListItemActions) {
        case E_TimeRangeSelectorTimeZoneListItemActions.setAsMain: {
          dispatch([E_TimeRangeSelectorActions.setMainTimeZone, timeZoneName]);
          return;
        }
        default:
          return;
      }
    }
  };

  return {
    handle24HourFormatToggle,
    handleTimeRangeChange,
    handleTimeZoneListItemAction
  };
}
